import React from "react";
import { Badge, message, Modal, Form, Icon, Input } from "antd";
import api from "../../api/api";
import { inject, observer } from 'mobx-react/index'

import { isAuthenticated } from "./../../utils/Session";
import moment from 'moment';
import 'moment/locale/zh-cn';
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'
const { TextArea } = Input;
@inject('headerStore') @observer

class Index extends React.Component {
    constructor(props) {
        super(props)
        this.props.headerStore.setCurrunt(this.props.location.pathname)

    }
    state = {
        status: 1,
        total: 0,
        backUserId: 1,
        list: [],
        pageNo: 1,
        visible: false,
        problemInfo: '',
        visibleReply: false,
        problemId: 0,
        isCheck: 1
    }
    componentWillMount() {
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId
        })
        this.getList(userInfo.backUserId).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages:res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    getList = async (adminId, step = 1, pageNo = 1, pageSize = 10) => {
        let res = api('getAllProblemList', 'post', {}, {
            adminId: adminId,
            pageNo: pageNo,
            pageSize: pageSize,
            step: step
        })
        return res
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    setStatus = (val) => {
        this.setState({
            status: val,
            pageNo:1
        })
        NProgress.start()
        this.getList(this.state.backUserId, val).then(res => {
            NProgress.done()

            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    pageNo: res.data.pageNumber,
                    pages:res.data.pages
                })
                if (val == 1) {
                    this.setState({

                        total: res.data.total,
                    })
                }
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    getProblemInfo = (val) => {
        this.setState({

            visible: true,
            problemId: val,

        })
        api('getProblemInfo', 'post', {}, {
            problemId: val
        }).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    problemInfo: res.data.data,
                    isCheck:res.data.data.step
                })

            } else {
                message.warning(res.data.errorMessage)
            }
        })

    }
    handleOk = () => {
        this.setState({
            visible: true
        })
    }
    handleCancel = () => {
        this.setState({
            visible: false
        })
    }
    handleReply = () => {
        this.setState({
            visibleReply: true
        })
    }
    handleReplyCancel = () => {
        this.setState({
            visibleReply: false
        })
    }
    getImg = (val) => {
        if (val) {
            if (val.imgUrl.indexOf(',') == -1) {
                return (<img src={val.imgUrl} alt="" />)
            } else {
                console.log(val.imgUrl.split(','))
                return val.imgUrl.split(',').map(res => {
                    return (<img src={res} alt="" />)
                })
            }
        }
    }
    getStep1 = (val) => {
        if (val) {
                return val.list.map((res,index) => {
                    if(res.step==1){
                        return (
                            (
                                <div className="step flexBox">
                                    <div className="step_time">
                                        <p>{moment(res.createTime).format("YYYY年MM月DD日")}</p>
                                        <p>{moment(res.createTime).format("a  hh:mm")}</p>
                                    </div>
                                    <div className="step_m">
                                        <div className={val.list.length-1==index&&val.step==1?'step_status status1':"step_status "}>
                                            <div></div>
                                        </div>
                                    </div>
                                    {
                                        res.reply?
                                            <div className="step_r flex">处理中：<span>{res.reply}</span></div>
                                       :
                                            <div className="step_r flex">处理中</div>

                                        
                                    }
                                </div>
                            )
                        )
                    }
                   
                })
            
        }
    }
    getStep2 = (val) => {
        if (val) {
            if (val.step == 2) {
                return (
                    <div className="step flexBox">
                        <div className="step_time">
                            <p>{moment(val.list[val.list.length-1].createTime).format("YYYY年MM月DD日")}</p>
                            <p>{moment(val.list[val.list.length-1].createTime).format("a  hh:mm")}</p>
                        </div>
                        <div className="step_m">
                            <div className="step_status status1">
                                <div></div>
                            </div>
                        </div>
                        <div className="step_r flex">处理成功：<span>{val.reply}</span></div>
                    </div>
                )
            } else {
                return (
                    <div className="step flexBox">
                        <div className="step_time">
                            <p>{moment().format("YYYY年MM月DD日")}</p>
                            <p>{moment().format("a  hh:mm")}</p>
                        </div>
                        <div className="step_m">
                            <div className="step_status noStatus">
                                <div></div>
                            </div>
                        </div>
                        <div className="step_r flex">处理成功</div>
                    </div>
                )
            }
        }
    }
    sendReply = () => {
        this.props.form.validateFields((err, values) => {
            if (!err) {
                api('replyProblem', 'post', {}, {
                    'problemId': this.state.problemInfo.id,
                    'reply': values.reply,
                    'adminId': this.state.backUserId,
                    'step': this.state.isCheck
                }).then(res => {
                    if (!res.data.errorMessage) {
                        message.success('回复成功！')
                        this.props.form.resetFields()
                        api('getProblemInfo', 'post', {}, {
                            problemId: this.state.problemInfo.id
                        }).then(res => {
                            if (!res.data.errorMessage) {
                                this.handleReplyCancel()
                                this.setState({
                                    problemInfo: res.data.data,
                                })

                            } else {
                                message.warning(res.data.errorMessage)
                            }
                        })
                        this.getList(this.state.backUserId,this.state.pageNo).then(res => {
                            if (!res.data.errorMessage) {
                                this.setState({
                                    list: res.data.list,
                                    total: res.data.total,
                                    pageNo: res.data.pageNumber,
                                    pages:res.data.pages
                                })
                            } else {
                                message.warning(res.data.errorMessage)
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }

                })
            }
        });

    }
    setCheck = (val) => {
        this.setState({
            isCheck: val
        })
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {

            this.getList(this.state.backUserId,this.state.status,this.state.pageNo-1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo - 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        list: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    setNext = () => {
        if (this.state.pageNo == this.state.pages) {
            return
        } else {

            this.getList(this.state.backUserId,this.state.status,this.state.pageNo+1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo + 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        list: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages
                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <div id="feedback">
                <div className="content">
                    <div className="head">
                        <Badge count={this.state.total} overflowCount={99}>
                            <span className={this.state.status == 1 ? 'statusActive feedBack' : 'feedBack'} onClick={() => { this.setStatus(1) }}>
                                待处理
                                </span>
                        </Badge>

                        <span className={this.state.status == 2 ? 'statusActive feedBack' : 'feedBack'} onClick={() => { this.setStatus(2) }}>已处理</span>

                    </div>
                    <div className="listBox">
                        {this.state.list && this.state.list.map(res => {
                            return (
                                <div className='list'>
                                    <div className="title">{res.problem}</div>
                                    <div className='type'>反馈类型：{res.type == 1 ? '功能吐槽' : res.type == 2 ? '系统故障' : '内容意见'}</div>
                                    <div className="time">{moment(res.createTime).format("YYYY年MM月DD日")}</div>
                                    <div className="handle" onClick={() => { this.getProblemInfo(res.id) }}>{ res.step==1?'处理':'查看'}</div>
                                    {
                                        res.step==1?<img className='stepImg' src={require('../../assets/images/img-step-1.png')} />:<img className='stepImg' src={require('../../assets/images/img-step-2.png')} />
                                    }
                                </div>
                            )
                        })}
                        {
                            this.state.list.length==0?<div className='noData'>
                                    <img src={require('./../../assets/images/img-nodata.png')} alt=""/>
                                    <p>暂无相关文章</p>
                                </div>:''
                        }
                        {
                        this.state.pages <= 1 ? '' : <div className='pagination'>
                            <span onClick={this.setPrev}>
                                <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                            <span>
                                {this.state.pageNo}/{this.state.pages}
                            </span>
                            <span onClick={this.setNext}>
                                <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                            </span>
                        </div>
                    }
                    </div>
                </div>
                <Modal
                    title=""
                    visible={this.state.visible}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width='52%'
                    wrapClassName='info'
                >
                    <div className="info_content flexBox">
                        <div className="flex_l">
                            <div className="headImg">
                                <img src={this.state.problemInfo && this.state.problemInfo.headImg} alt="" />
                            </div>
                        </div>
                        <div className="flex info_m">
                            <div className="info_title">
                                {this.state.problemInfo && this.state.problemInfo.problem}
                            </div>
                            <div className="imgList">
                                {
                                    this.getImg(this.state.problemInfo && this.state.problemInfo)
                                }
                            </div>
                        </div>
                    </div>
                    <div className="steps">
                        <div className="step_title">
                            进度条:
                        </div>
                        <div className="step_list">
                            <div className="step flexBox">
                                <div className="step_time">
                                    <p>{moment(this.state.problemInfo && this.state.problemInfo.createTime).format("YYYY年MM月DD日")}</p>
                                    <p>{moment(this.state.problemInfo && this.state.problemInfo.createTime).format("a  hh:mm")}</p>
                                </div>
                                <div className="step_m">
                                    <div className="step_status">
                                        <div></div>
                                    </div>
                                </div>
                                <div className="step_r flex">发表意见</div>
                            </div>
                            {
                                this.getStep1(this.state.problemInfo)
                            }
                            {
                                this.getStep2(this.state.problemInfo)
                            }
                        </div>
                    </div>
                    <div className="btn">
                        {this.state.problemInfo.step==2?'':<span className='active' onClick={this.handleReply}>回复</span>}
                        <span onClick={this.handleCancel}>取消</span>
                    </div>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.visibleReply}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleReply}
                    onCancel={this.handleReplyCancel}
                    width='47%'
                    wrapClassName='reply'
                >
                    <div className="reply_content ">
                        <div className="status flexBox">
                            <div className="status_label">
                                选择状态:
                            </div>
                            <div className="flex flexBox">
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(1) }}>
                                        <span className={this.state.isCheck == 1 ? 'active' : ''}>
                                            {this.state.isCheck == 1 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        处理中
                                    </span>
                                </div>
                                <div className="flex">
                                    <span onClick={() => { this.setCheck(2) }}>
                                        <span className={this.state.isCheck == 2 ? 'active' : ''} >
                                            {this.state.isCheck == 2 ? <Icon type="check" style={{ 'fontSize': '14px', 'color': '#fff' }} /> : ''}
                                        </span>
                                        处理完成
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='replyInfo'>
                        <Form>
                            <Form.Item>
                                {getFieldDecorator('reply', {
                                    rules: [{ required: true, message: '请输入回复内容！' }],
                                })(
                                    <TextArea
                                        placeholder="输入回复内容"
                                        autosize={{ minRows: 6 }}
                                    />
                                )}
                            </Form.Item>

                        </Form>
                    </div>
                    <div className="btn">
                        <span className='active' onClick={this.sendReply}>回复</span>
                        <span onClick={this.handleReplyCancel}>取消</span>
                    </div>
                </Modal>
            </div>
        )
    }
}
const WrappedRegistrationForm = Form.create()(Index);
export default WrappedRegistrationForm